export default Ember.HTMLBars.template({"id":"dvw61RjF","block":"{\"symbols\":[],\"statements\":[[7,\"h2\"],[9],[0,\"Cross-Dating\"],[10],[0,\"\\n\"],[7,\"hr\"],[11,\"class\",\"underline\"],[9],[10],[0,\"\\n\"],[7,\"h4\"],[9],[0,\"2018-11-17 - Alexander Mattheis\"],[10],[0,\"\\n\"],[7,\"p\"],[11,\"class\",\"content\"],[9],[0,\"\\n  The correct dates of wood pieces can be determined by annual rings.\\n  Therefore, one can just measure the widths or maximum densities of individual rings\\n  and then shift them in a chronology with known dates to determine the exact calendar year.\\n  But such established approaches do not work well with short pieces,\\n  i.e. with wood pieces containing only a few annual rings.\\n  My new approach which I have developed in cooperation with Dr. Martin Raden\\n  and PD Dr. Hans-Peter Kahle, however, is different.\\n  It has been shown that it can accurately determine the date for even shorter wood pieces correctly.\\n  Therefore, it uses series of densities within a ring.\\n  By this it becomes one of the most accurate, existing approaches in dendrochronology, today!\\n\"],[10],[0,\"\\n\\n\"],[7,\"p\"],[11,\"class\",\"content\"],[9],[0,\"\\n  Information about how this grandiose approach works,\\n  you see below in the presentation which I held on the fifth of September 2018.\\n\"],[10],[0,\"\\n\\n\"],[7,\"div\"],[11,\"class\",\"embed-responsive embed-responsive-16by9\"],[9],[0,\"\\n  \"],[7,\"iframe\"],[11,\"class\",\"embed-responsive-item\"],[11,\"src\",\"https://www.youtube-nocookie.com/embed/p4GZlmuU7d8\"],[11,\"allow\",\"autoplay; encrypted-media\"],[11,\"allowfullscreen\",\"\"],[9],[10],[0,\"\\n\"],[10],[0,\"\\n\\n\"],[7,\"p\"],[11,\"class\",\"content\"],[9],[0,\"\\n  The software was written with the help of the statistical programming language\\n  \"],[7,\"a\"],[11,\"href\",\"https://www.r-project.org/\"],[11,\"target\",\"_blank\"],[11,\"rel\",\"noopener\"],[9],[0,\"R\"],[10],[0,\" and has been released\\n  under the \"],[7,\"a\"],[11,\"href\",\"https://opensource.org/licenses/MIT\"],[11,\"target\",\"_blank\"],[11,\"rel\",\"noopener\"],[9],[0,\"MIT licence\"],[10],[0,\",\\n  such that even a commercial use is still possible.\\n\"],[10],[0,\"\\n\\n\"],[7,\"b\"],[9],[0,\"Did you get interested?\"],[10],[0,\" \"],[7,\"a\"],[11,\"href\",\"https://github.com/AlexanderMattheis/Cross-Dating\"],[11,\"target\",\"_blank\"],[11,\"rel\",\"noopener\"],[9],[0,\"Cross-Dating\"],[10],[0,\"\\n\"]],\"hasEval\":false}","meta":{"moduleName":"bitcorn/templates/downloads/programs/cross-dating.hbs"}});